@import "../assets/sass/variables";

.linkBox {
  position: relative;
  font-size: 30px;
  text-transform: uppercase;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  text-align: right;
  padding-right: 5%;
  color: #fff;
  text-shadow: 1px 1px 1px #a2a2a2;
  width: 40%;
  height: 100%;
  background: rgb(232, 235, 243, 0.8);
  z-index: 1000
}


.white {
  color: black;

}

.accent {
  color: #b4480d;
  margin-left: 8px;
}

.link{
  height: 30px;
  width: 100%;
  text-decoration: none;
  padding-right: 15%;
  transition: all .5s ease;


}

.link:hover{
  font-size: 33px;


}

@media (max-width: 600px) {
  .linkBox {
    width: 100%;
    height: 35%;
    margin-bottom: 20px;
    border: none;
    padding: 0;
    font-size: 20px;
    justify-content: space-between;
    text-align: center;

  }
}